.cardContent {
  height: 35px;
  /* position: relative; */

  outline: 1px solid #00000088;
  border-radius: 4px;

  box-shadow: -2px 2px 3px #00000055, inset -1px 1px 2px #ffffff99;

  cursor: grab;
  user-select: none;

  overflow: hidden;

  /* background: linear-gradient(180deg, transparent 33%, #ffffff44 66%); */
}

