.empty { 
  min-width: 25px; max-width: 57px; 
  width: 100%; height: 35px;
  line-height: 35px;
  border-radius: 4px;
  box-shadow: inset 1px -1px 1px #ffffff80, inset -2px 2px 6px #00000040;

  font-size: .9em;
  font-weight: bold;
  text-align: center;
  color: #ffffff;

  overflow-x: hidden;

  user-select: none;
}